import { ADD_PENDING_SLUG, CLEAR_PENDING_SLUG } from "./actionTypes";

const initialState = {
    pendingSlug: null
};

export default function(state = initialState, action) {
    switch (action.type) {
        case ADD_PENDING_SLUG:
            return {
                ...state,
                pendingSlug: action.payload
            };
        case CLEAR_PENDING_SLUG:
            return {
                ...state,
                pendingSlug: null
            };
        default:
            return state;
    }
}
